<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part v-loading="newLoading">
      <el-tabs v-model="tabPick" @tab-click="handleClick">
        <el-tab-pane label="早自习到岗" name="MORNING_LESSON"></el-tab-pane>
        <el-tab-pane label="课间操跟跑到岗" name="BREAK_EXERCISE"></el-tab-pane>
        <el-tab-pane label="晚自习到岗" name="EVENING_LESSON"></el-tab-pane>
        <el-tab-pane label="例会到岗" name="REGULAR_MEETING"></el-tab-pane>
      </el-tabs>
      <div class="btn-block btns">
        <div class="btn-main mr15" @click="dialog=true">导入</div>
        <div class="btn-main mr15" @click="ClickDateLine">导出</div>
        <div class="btn-main-deepen mr15"
             @click="$router.push(`/student-work/appraisal/early-selfStudy/entry?tabPick=`+tabPick+``)">录入检查结果
        </div>
        <div class="btn-main-deepen"
             @click="$router.push(`/student-work/appraisal/early-selfStudy/recording?tabPick=`+tabPick+``)">查询检查记录
        </div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <!--年级组长-->
          <th width="20%" v-if="table.query.teacherPosition==='GRADE_LEADER'">年级</th>

          <th width="20%" v-if="table.query.teacherPosition==='HEADMASTER'">班级</th>
          <th width="20%" v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">姓名</th>
          <th width="20%" v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">缺席</th>
          <th width="20%" v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">迟到</th>
          <th width="20%" v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">总计</th>
          <!--班主任助理-->
          <th width="33%" v-if="table.query.teacherPosition==='HEADMASTER_ASSISTANT'">班级</th>
          <th width="33%" v-if="table.query.teacherPosition==='HEADMASTER_ASSISTANT'">姓名</th>
          <th width="33%" v-if="table.query.teacherPosition==='HEADMASTER_ASSISTANT'">到位次数</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <!--年级组长-->
          <td v-if="table.query.teacherPosition==='GRADE_LEADER'">{{item.gradeName}}</td>

          <td v-if="table.query.teacherPosition==='HEADMASTER'">{{item.className}}</td>
          <td v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">{{item.teacherName}}</td>
          <td v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">{{item.absentNum}}</td>
          <td v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">{{item.lateNum}}</td>
          <td v-if="table.query.teacherPosition!=='HEADMASTER_ASSISTANT'">{{item.totalNum}}</td>
          <!--班主任助理-->
          <td v-if="table.query.teacherPosition==='HEADMASTER_ASSISTANT'">{{item.className}}</td>
          <td v-if="table.query.teacherPosition==='HEADMASTER_ASSISTANT'">{{item.teacherName}}</td>
          <td v-if="table.query.teacherPosition==='HEADMASTER_ASSISTANT'">{{item.presentNum}}</td>
        </tr>
        </tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>

      <!--导入-->
      <el-dialog title="导入" class="dialog" :visible.sync="dialog" width="540px">
        <div class="item">
          <span class="title imp">日期</span>
          <el-date-picker
                  type="date"
                  v-model="inspectDate"
                  placeholder="选择日期" style="width: 86%">
          </el-date-picker>
        </div>
        <import-excel ref="importExcel" @change="change" :type="templateName"/>
        <div class="dialog_btns">
          <div @click="impFun" class="btn-submit btn-upload mr15">确认</div>
          <div @click="dialog = false" class="btn-clear">返回</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data () {
      return {
        nav: [{name: null, path: ''}],
        searchData: [
          {type: 'select', title: '职务', model: 'teacherPosition', option: this.$enums.TeacherPosition.list},
          {type: 'select', title: '年级', model: 'gradeId', option: []},
          {type: 'cascader', title: '月份/周', modelName: ['month', 'weekId'], option: []},
        ],
        table: {
          url: '/assess/queryAssessInspectRecordStatPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            // 月份
            month: null,
            // 年级ID
            gradeId: null,
            // 教师职务
            teacherPosition: 'HEADMASTER',
            // 周次ID
            weekId: null,
          }
        },
        tabPick: this.$route.query.tabPick || 'MORNING_LESSON',
        newLoading: false,

        // 导入
        dialog: false,
        files: null,
        inspectDate: null,

        templateName: null
      }
    },
    watch: {
      'dialog': function () {
        if (!this.dialog) {
          this.inspectDate = null
          this.$refs.importExcel.$data.fileName = null
        }
      }
    },
    methods: {
      // 处理时间--导出
      ClickDateLine(){
        let newDate = this.table.query.month
        if (this.table.query.month){
          this.table.query.month = this.table.query.month.substr(0,10)
          this.table.query.month = this.table.query.month.replace(/(\d{4})-(\d{2})-(\d{2})/g,'$1/$2/$3')
        }
        this.$exportDetail('/export/assessInspectRecordExport.do')
        this.table.query.month = newDate
      },
      // 切换列表
      handleClick () {
        this.$router.replace(`/student-work/appraisal/early-selfStudy?tabPick=${this.tabPick}`)
        // 通过TabClick点击更换tabPick 改变面包屑导航name
        this.nav[0].name = this.$enums.InspectType.getName(this.tabPick)
        // 通过TabClick点击更换tabPick 请求不同检查类型的列表页
        this.table.query.inspectType = this.tabPick
        this.$search(this.table)
      },
      // 获取年级列表
      async getGradeList () {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.searchData[1].option = []
        res.forEach(e => {
          this.searchData[1].option.push({
            label: e.name,
            value: e.id
          })
        })
        this.table.query.gradeId = res[0].id
      },
      // 获取月周级联
      async getWeek () {
        this.searchData[2].option = await this.$ajax({
          url: '/school/queryMonthWeekCascade.do'
        })
      },
      change (e) {
        this.files = e.target.files[0]
      },
      // 导入
      async impFun () {
        if (!this.inspectDate) {
          this.$message.warning('请选择日期')
          return
        }
        let file = this.files
        if (!file) {
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('inspectDate', this.inspectDate)
        formData.append('inspectType', this.tabPick)
        let obj = {
          url: '/import/importAssessInspectRecord.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          // this.$refs.upload.value = null
          this.$remind('导入成功')
          this.dialog = false
          this.$search(this.table)
        }
      },
    },
    async mounted () {
      try {
        this.newLoading = true
        // 通过tabPick 更换面包屑导航name
        this.nav[0].name = this.$enums.InspectType.getName(this.tabPick)
        await this.getGradeList()
        await this.getWeek()
        // 通过tabPick 请求不同检查类型的列表页
        this.table.query.inspectType = this.tabPick
        this.$search(this.table)
        //$enums.InspectType.getName(this.tabPick)+'导入模板'
        this.templateName = this.$enums.InspectType.getName(this.tabPick) + '导入模板'
        if (this.tabPick === 'BREAK_EXERCISE') {
          this.templateName = '课间操到岗导入模板'
        }
        if (this.tabPick === 'REGULAR_MEETING') {
          this.templateName = '班主任例会到岗导入模板'
        }
        this.newLoading = false
      } catch (e) {
        this.newLoading = false
      }
    },
  }
</script>
<style lang="sass" scoped>
    .dialog
      .title
        width: 70px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .input
        width: calc(100% - 110px)
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .file-input
        /deep/ input
          padding-top: 10px
          line-height: 1
      .item
        margin-bottom: 20px
      .dialog_btns
        margin-top: 15px
        text-align: center
</style>
